import support from '../../store/support'
export default {
  props: ['id', 'datainput'],
  data() {
    return {
      support: support.deliverableState,
      dialogPDFLoading: true,
      isCallBack: true,
      pdfBlob: null,
      logs: []
    }
  },
  created() {
    var vm = this
    vm.$nextTick(function() {
      setTimeout(() => {
        console.log(vm.support.deliverableState)
        vm.pullLogs()
      }, 100)
    })
  },
  methods: {
    pullLogs() {
      let vm = this
      vm.$store.dispatch('viewLogs', vm.id).then(function(result) {
        vm.logs = result
      })
    }
  }
}
